<template>
<briup-fulllayout title="修改地址">
<div>
    <van-cell v-for="item in address"
        :key="item.id"
        :title="item.province+' '+item.city+' '
        +item.area+' '+item.address" >
    </van-cell>
<van-cell-group>
  <van-field v-model="form.telephone" placeholder="请输入手机号"/>
</van-cell-group>
<van-cell-group>
  <van-field v-model="form.province" placeholder="省" />
</van-cell-group>
<van-cell-group>
  <van-field v-model="form.city" placeholder="市" />
</van-cell-group>
<van-cell-group>
  <van-field v-model="form.area" placeholder="区" />
</van-cell-group>
<van-cell-group>
  <van-field v-model="form.address" placeholder="详细地址" />
</van-cell-group>

<van-button block type="primary"  @click="submitHandler">保存</van-button>

</div>
</briup-fulllayout>
</template>
<script>
import { mapState } from 'vuex'
import { post } from '../../../http/axios'
export default{
    
    data(){
        return{
            address:[],
            form:{}
        }
    },
    computed:{
        ...mapState("user",["info"])
    },
    methods:{
    submitHandler(){
            let url = "/address/saveOrUpdate"
            //在提交前将登陆人的id作为顾客id
            this.form.customerId = this.info.id;
            let data = {
                province:this.info.province,
                city:this.city,
                address:this.address
            }
            post(url,this.form).then((response)=>{
                //返回上一个界面
                this.$router.go(-1);
                //轻提示
                this.$toast.success(Response.message);
            })
        }
    }
}
</script>