<template>
<briup-fulllayout title="常用地址">
<!-- {{addresses}} -->
<!-- {{info}} -->
    <van-list>
    <van-cell v-for="item in addresses"
        :key="item.id"
        :title="item.province+' '+item.city+' '
        +item.area+' '+item.address" >
        
        <a href="" @click.prevent="toDeleteHandler(item.id)">删除</a>
          <a href="" @click.prevent="toUpdateHandler(item.id)">修改</a>
    </van-cell>
        
    </van-list>
    
    <br/>
    
    <van-button type="primary" block @click="toAddressEditHandler">添加地址</van-button>
</briup-fulllayout>
</template>

<script>
import {get} from '../../../http/axios'
import {mapState} from 'vuex'

export default{
    data(){
    return{
        addresses:[],
        form:{
        type:"address"
      }
    }
},
computed:{//计算属性
//将状态机中的user对象中的info对象获取到
    ...mapState("user",["info"])
},
created(){
this.loadAddress();
},
methods:{
    toUpdateHandler(){
        this.$router.push("/manager/address_change"),
        this.addresses = id;
        let id = this.info.id;
        let telephone = this.info.telephone;
        // let url ="/address/saveOrUpdate?id="+id;
        // get(url).then((response)=>{
          
        // })  
    },
    toDeleteHandler(id){
        let url ="/address/deleteById?id="+id;
        get(url).then((response)=>{
           this.addresses =  response.data;
        })  
         this.$router.go(0);
         this.$toast.success("删除成功");
    },
    toAddressEditHandler(){
        this.$router.push("/manager/address_edit")
    },
    loadAddress() {
        // 调用当前顾客地址信息
        let id = this.info.id;
        let url="/address/findByCustomerId?id="+id;
        get(url).then((Response)=>{
            this.addresses=Response.data;
        })
    }
}
}
</script>